.mobile-menu {
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  padding-top: 10px;
  margin-top: 16px;
  border-top: 1px solid #ddd;
  display: none;

  @include medium-size-screen {
    display: block;
  }

  > .item {
    line-height: 36px;

    a {
      color: $color-dark;
    }
  }
}